<template>
  <section id="sideBar" class="nav-list" :class="{'show':showMenu}">
    <div class="logo-info">
      <img class="logo" src="http://s3.pstatp.com/resource/neihan_wap/static/image/joke_jokeessay_new_c0cceed.png" alt="">
      <div class="desc">
        <h2>内涵段子</h2>
        <p>搞笑段子、图片 <br>  视频火爆聚集地</p>
      </div>
    </div>
    <section class="list-ul">
      <router-link class="iconfont icon-tuijian  item" :to="{'name':'list',query:{list_id:'-101'}}">推荐</router-link>
      <router-link class="iconfont icon-shipin  item" :to="{'name':'list',query:{list_id:'-104'}}">视频</router-link>
      <router-link class="iconfont icon-xiu  item" :to="{'name':'list',query:{list_id:'-301'}}">段友秀</router-link>
      <router-link class="iconfont icon-tupian  item" :to="{'name':'list',query:{list_id:'-103'}}">图片</router-link>
      <router-link class="iconfont icon-jingcaiduanzi2  item" :to="{'name':'list',query:{list_id:'-102'}}">段子</router-link>
    </section>
  </section>
</template>
<script>
    export default {
        replace: true,
        props: ['showMenu', 'pageType']
    };
</script>

<style lang="scss">
    /*侧边栏*/

    .nav-list {
        position: fixed;
        top: 0;
        bottom: 0;
        left: -200px;
        width: 200px;
        background-color: #fff;
        color: #313131;
        transition: all .3s ease;
        z-index: 99;
        &.show {
            transform: translateX(200px);
        }
    }

    .logo-info {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-bottom: 15px;
        border-bottom: 1px dashed #ff819f;
    }

    .logo {
        width: 50px;
        border-radius: 50%;
        margin: 15px 15px 0 15px;
    }

    .desc {
        flex: 1;
        margin-top: 15px;
        margin-right: 15px;
        h2 {
            font-size: 18px;
            margin-bottom: 5px;
        }
        p {
            font-size: 12px;
            line-height: 1.5;
        }
    }
    /*侧边栏列表*/

    .list-ul {
        margin: 0 24px;
        overflow: hidden;
        padding-top: 9%;
        .item {
            display: block;
            font-size: 20px;
            padding: 9% 0;
            text-align: left;
            text-indent: 1px;
            line-height: 20px;
            color: #ff819f;
            font-weight: 500;
            text-decoration: none;
        }
    }

    .iconfont {
        font-style: normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }

    .icon-tupian:before,
    .icon-xiu:before,
    .icon-shipin:before,
    .icon-jingcaiduanzi2:before,
    .icon-tuijian:before {
        content: '';
        display: inline-block;
        width: 20px;
        height: 20px;
        margin-right: 20px;
        vertical-align: top;
    }

    .icon-tupian:before {
        background: url(data:image/png;base64,) no-repeat;
        background-size: 100%;
    }

    .icon-xiu:before {
        background: url(data:image/png;base64,) no-repeat;
        background-size: 100%;
    }

    .icon-shipin:before {
        background: url(data:image/png;base64,) no-repeat;
        background-size: 100%;
    }

    .icon-jingcaiduanzi2:before {
        background: url(data:image/png;base64,) no-repeat;
        background-size: 100%;
    }

    .icon-tuijian:before {
        background: url(data:image/png;base64,) no-repeat;
        background-size: 100%;
    }
</style>



